div.next {
    width: 150px;
    height: 150px;
}

div.next img {
    max-width: 125px;
}

div.leading {
    width: 200px;
    height: 200px;
}

div.leading img {
    max-width: 175px;
}

/* either cornflowerblue or
    color: rgb(46, 188, 244); */
div.box {
    border: 4px solid rgb(46, 188, 244);
    margin: 5px;
    border-radius: 20px;
    position: relative;
    display: inline-block;
}

div.box img {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%)
}

@media only screen and (max-width: 500px) {
    div.next {
        width: 125px;
        height: 125px;
    }
    
    div.next img {
        max-width: 100px;
    }
    
    div.leading {
        width: 160px;
        height: 160px;
    }
    
    div.leading img {
        max-width: 135px;
    }
}