@import url(https://fonts.googleapis.com/css?family=Raleway:700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}


/* @import url('https://fonts.googleapis.com/css?family=Raleway:700&display=swap'); */

.main {
    height: 100vh;
    border: 3px;
    display: inline-block
}

h1.title {
    font-family: 'Raleway', sans-serif;
    font-size: 70px;
    color: rgb(46, 188, 244);
}

h1.title:hover {
    cursor: default;
}
  
@media only screen and (max-width: 500px) {
    h1.title {
        font-size: 56px;
    }
}
@media only screen and (max-width: 720px) {
    .queue {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    
}
div.next {
    width: 150px;
    height: 150px;
}

div.next img {
    max-width: 125px;
}

div.leading {
    width: 200px;
    height: 200px;
}

div.leading img {
    max-width: 175px;
}

/* either cornflowerblue or
    color: rgb(46, 188, 244); */
div.box {
    border: 4px solid rgb(46, 188, 244);
    margin: 5px;
    border-radius: 20px;
    position: relative;
    display: inline-block;
}

div.box img {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%)
}

@media only screen and (max-width: 500px) {
    div.next {
        width: 125px;
        height: 125px;
    }
    
    div.next img {
        max-width: 100px;
    }
    
    div.leading {
        width: 160px;
        height: 160px;
    }
    
    div.leading img {
        max-width: 135px;
    }
}
.counterContainer {
    display: flex;
    justify-content: center;
}

.counter {
    background-color: rgba(0,0,0,0.40);
    width: 400px;
    height: 100%;
    margin: 20px 0 20px 0;
    border-radius: 10px;
}

img.smallPiece {
    width: 50px;
    height: 50px;
}

img.smallPiece.bright {
    -webkit-filter: contrast(100%) brightness(100%);
            filter: contrast(100%) brightness(100%);
}

img.smallPiece.dimmed {
    -webkit-filter: contrast(0%) brightness(50%);
            filter: contrast(0%) brightness(50%);
}

@media only screen and (max-width: 400px) {
    .counter {
        border-radius: 0;
    }
}
.sectioncontainer {
    /* display: grid;
    grid-template-columns: 50% 50%; */
    display: flex;
    flex-direction: column;
}

.info {
    background-color: rgb(46, 188, 244);
}

.infobox h1, p, ul {
    color: white;
}

.infobox h1 {
    font-family: 'Raleway', sans-serif;
    font-size: 3.5em;
}

.infobox p, ul {
    font-family: 'Roboto', sans-serif;
    font-size: 1.2em;
    text-align: left
}

.infobox {
    margin: 2.5em
}

@media only screen and (max-width: 1000px) {
    .sectioncontainer {
        display: flex;
        flex-direction: column;
    }
}
.footer {
    background-color: rgb(10, 19, 33);
    min-height: 5em;
    display: flex;
    flex-direction: column
}

p {
    color: white;
}

.regulartext {
    display: inline-block;
    margin: 0.7em;
}

.disclaimer {
    display: inline-block;
    margin: 0.5em;
}

.disclaimer p {
    font-size: 12px;
}
